body {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.stopwatch {
  width: 80vmin;
  height: 80vmin;
  border: 1vmin dotted #000;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;

  &__center {
    width: 5vmin;
    height: 5vmin;
    background-color: #f6a603;
    border-radius: 50%;
    position: absolute;
  }

  &__minutes {
    width: 3vmin;
    height: 20vmin;
    background-color: #0700ff;
    position: absolute;
    bottom: 50%;
    animation: rotate 3600s infinite steps(60, end);
    transform-origin: bottom center;
  }

  &__seconds {
    width: 1.5vmin;
    height: 38vmin;
    background-color: #2c8000;
    position: absolute;
    bottom: 50%;
    animation: rotate 60s infinite linear;
    transform-origin: bottom center; /* Додано цей рядок */
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
    transform-origin: bottom center;
  }
  to {
    transform: rotate(360deg);
    transform-origin: bottom center;
  }
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 600s;
  animation-timing-function: steps(60);
}
